import React, { useEffect } from 'react';
import { ethers } from 'ethers';
import { FAUCET_ABI } from '../../abi';
import { Network } from '../../networks';
import { toast } from 'react-toastify';
import { MetamaskError, toastArgs } from '../../utils';

interface Props {
    network: Network | null;
    address: string;
}

const addresses = [
    '0x5e513A8D7ad859169AeC5678478f79C43eDdAcb1',
    '0x4273E6941aAe56Dd58E251237cf0C3f68cE58AC5',
    '0x4D9fd2529e007b65917EC2e56F3155527c54983f',
];

const FaucetButton: React.FC<Props> = (props: Props) => {
    const handleClaimToken = async () => {
        try {
            const provider = await new ethers.providers.Web3Provider(window.ethereum)
            const faucetContract = new ethers.Contract(
                (props.network as Network).faucetAddress as string,
                FAUCET_ABI,
                provider.getSigner(props.address),
            )
            const tx = await faucetContract.claimTestTokens(addresses)
            tx.wait()
                .then((receipt: any) => {
                    if (receipt && receipt.status) {
                        toast.success('successfully claimed test token 🎉', toastArgs)
                    }
                })
                .catch((error: any) => {
                    if (error) {
                        toast.error('Already claimed or transaction failed 🙁', toastArgs)
                    }
                })
        } catch (err) {
            const error = err as MetamaskError
            toast.error(error.data ? error.data.message : error.message, toastArgs)
        }
    };

    useEffect(() => {
        // do nothing
    }, [props.network, props.address])

    return (
        <>
            {props.network && props.network.chainId === '0x1c' ? (
                <div className="network-button" onClick={() => handleClaimToken()}>
                    <div className="network-button-content">
                        <span className="network-name">Claim Test Token</span>
                    </div>
                </div>
            ) : null}
        </>
    )
};

export default FaucetButton;
